// 
import { request } from '@/utils/request'

// 
const login = (user) => {
  return request({
    method: 'POST',
    url: '/v1_0/authorizations',
    data: user
  })
}
// 
const sendSms = mobile => {
  return request({
    method: 'GET',
    url: `/v1_0/sms/codes/${mobile}`
  })
}
// 
const getCurrentUser = () => {
  return request({
    method: 'GET',
    url: '/v1_0/user'
  })
}
// 
const getUserProfile = () => {
  return request({
    method: 'GET',
    url: '/v1_0/user/profile'
  })
}
// 
const updateUserProfile = (data) => {
  return request({
    method: 'PATCH',
    url: '/v1_0/user/profile',
    data
  })
}
// 
const updateUserPhoto = (data) => {
  return request({
    method: 'PATCH',
    url: '/v1_0/user/photo',
    data
  })
}
// 
const getUserChannels = () => {
  return request({
    method: 'GET',
    url: '/v1_0/user/channels'
  })
}
// 
const addFollow = (target) => {
  return request({
    method: 'POST',
    url: '/v1_0/user/followings',
    data: {
      target
    }
  })
}
// 
const cancelFollow = (target) => {
  return request({
    method: 'DELETE',
    url: `/v1_0/user/followings/${target}`
  })
}
export {
  login,
  sendSms,
  getCurrentUser,
  getUserChannels,
  addFollow,
  cancelFollow,
  getUserProfile,
  updateUserProfile,
  updateUserPhoto
}
