<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      class="app-nav-bar"
      title=""
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <!--  -->
    <!-- acceptimage -->
    <input type="file" hidden ref="file" accept="image/*" @change="onFileChange"/>
    <van-cell center title="" is-link @click="$refs.file.click()">
      <template #default>
        <van-image
          style="vertical-align:middle"
          width="40"
          height="40"
          round
          fit="cover"
          :src="user.photo"
        ></van-image>
      </template>
    </van-cell>
    <van-cell title="" is-link :value="user.name" @click="isEditNameShow=true"></van-cell>
    <van-cell title="" is-link :value="user.gender===0 ? '' : ''" @click="isEditGenderShow=true"></van-cell>
    <van-cell title="" is-link :value="user.birthday" @click="isEditBirthdayShow=true"></van-cell>
    <!--  -->
    <van-popup
      v-model="isEditNameShow"
      position="bottom"
      :style="{height:'100%'}"
    >
      <!--
        v-if
        
        
      -->
      <UpdateName :name="user.name" v-if="isEditNameShow"></UpdateName>
    </van-popup>
    <!--  -->
    <van-popup
      v-model="isEditGenderShow"
      position="bottom"
    >
      <UpdateGender :gender="user.gender" v-if="isEditGenderShow"/>
    </van-popup>
    <!--  -->
    <van-popup
      v-model="isEditBirthdayShow"
      position="bottom"
    >
      <UpdateBirthday :birthday="user.birthday" v-if="isEditBirthdayShow"/>
    </van-popup>
    <!--  -->
    <van-popup
      v-model="isEditPhotoShow"
      position="bottom"
      :style="{height: '100%'}"
      style="background:#000;"
    >
      <UpdatePhoto :file="previewImage" v-if="isEditPhotoShow"/>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user'
import UpdateName from './components/update-name'
import UpdateGender from './components/update-gender'
import UpdateBirthday from './components/update-birthday'
import UpdatePhoto from './components/update-photo'
import { eventBus } from '@/utils/eventBus'

export default {
  name: 'UserProfile',
  components: {
    UpdateName,
    UpdateGender,
    UpdateBirthday,
    UpdatePhoto
  },
  data () {
    return {
      user: {}, // 
      isEditNameShow: false, // 
      isEditGenderShow: false, // 
      isEditBirthdayShow: false, // 
      isEditPhotoShow: false, // 
      previewImage: null // 
    }
  },
  mounted () {
    this.loadUserProfile()
    eventBus.on('closeUpdateNamePopUp', () => {
      // 
      this.isEditNameShow = false
    })
    eventBus.on('updateNameSuccess', (name) => {
      // 
      this.user.name = name
      this.isEditNameShow = false
    })
    eventBus.on('closeUpdateGenderPopup', () => {
      // 
      this.isEditGenderShow = false
    })
    eventBus.on('updateGenderSuccess', gender => {
      // 
      this.user.gender = gender
      this.isEditGenderShow = false
    })
    eventBus.on('closeUpdateBirthdayPopup', () => {
      // 
      this.isEditBirthdayShow = false
    })
    eventBus.on('updateBirthdaySuccess', birthday => {
      // 
      this.user.birthday = birthday
      this.isEditBirthdayShow = false
    })
    eventBus.on('closeUpdatePhotoPopup', () => {
      // 
      this.isEditPhotoShow = false
    })
    eventBus.on('updateUserPhotoSuccess', photo => {
      // 
      this.user.photo = photo
      this.isEditPhotoShow = false
    })
  },
  methods: {
    async loadUserProfile () {
      const { data } = await getUserProfile()
      this.user = data.data
    },
    // 
    onFileChange () {
      const fileIpt = this.$refs.file
      this.previewImage = fileIpt.files[0]
      this.isEditPhotoShow = true
      // valuechange
      fileIpt.value = ''
    }
  }
}
</script>

<style lang="less" scoped>
.van-popup {
  background-color: #f5f7f9;
}
</style>
